#Search {
    color: #fff;
    width: 100%;

    .search__container {
        display: flex;
        flex-wrap: wrap;
        min-height: 130px;
        background-color: #594731;
        justify-content: space-around;
        align-items: center;
        padding-top: 15px;
        padding-bottom: 15px;
    }

    h2 {
        white-space: nowrap;
    }

    .inputWrap {
        position: relative;
        display: flex;
        flex-direction: column;
        margin: 0 5px;

        .checkIn {
            width: 260px;
            height: 50px;
        }

        .checkOut {
            width: 260px;
            height: 50px;
        }

        .person {
            width: 90px;
            height: 50px;
        }
    }

    @media (max-width: 767.98px) {
        .search__container {
            flex-direction: column;
        }

        .inputWrap {
            align-items: center;
            margin: 5px 0;
        }
    }

    label {
        text-align: start;
    }

    .calendarWrap {
        position: absolute;
        top: 78px;
        left: 2px;
        background-color: #fff;
        color: #000;
        opacity: 0;
        transform: translateY(-10px);
        visibility: hidden;
        transform: 0.3s;
    }

    .calendarWrap.active {
        transform: translateY(0);
        opacity: 1;
        visibility: visible;
    }

    .checkBtn {
        background-color: #DEC328;
        color: #fff;
        width: 175px;
        height: 50px;
        border-radius: 5px;
        margin-top: 10px;
    }

    .checkBtn:hover {
        background-color: #cfb626;
        cursor: pointer;
    }

    input {
        padding: 4px;
        margin-top: 4px;
    }
}