@media (min-width: 768px) {
    .About {
        margin: 0 80px;
    }
}

.About {
    display: flex;
    flex-direction: column;
    width: 100%;
    
    .block {
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        text-align: start;
        margin-top: 40px;

    }

    .text1 {
        display: flex;
        max-width: 57%;

        h1 {
            font-size: 24px;
            line-height: 40px;
            min-width: 110px;
        }

    }
    
    p {
        margin-top: 40px;
    }
    
    .block p {
        max-width: 1250px;
    }

    img {
        margin: 10px;
        max-width: 100%;
    }

    @media (max-width: 767.98px) {
        .text1 {
            max-width: 100%;
        }
    }
}