#RoomsPage {
    width: 100%;

    .head {
        background-image: url("../../../assets/img/rooms/background.png");
        background-size: 100% 100%;
        color: #fff;
        height: 330px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;

        h1 {
            font-size: 32px;
            margin-bottom: 30px;
        }

        p {
            max-width: 500px;
        }
    }


    .content {
        display: flex;
        flex-direction: column;
        width: 100%;

        .RoomComponent {
            display: flex;
            justify-content: center;
        }
    }

    @media (max-width: 767.98px) {
        .room {
            flex-wrap: wrap;
        }
    }

    .room {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-top: 80px;
        width: 90%;

        .description {
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            text-align: start;
            max-width: 670px;

            h2 {
                font-size: 40px;
                margin-bottom: 16px;
            }

            p {
                margin-bottom: 30px;
            }

            .button-wrap {
                width: 100%;
                display: flex;
                justify-content: center;
            }
        }
    }

    img {
        max-width: 100%;
    }

    .bookNowBtn {
        background-color: #DEC328;
        border: solid 1px #000;
        width: 214px;
        height: 61px;
    }

    .bookNowBtn:hover {
        background-color: #cab124;
        cursor: pointer;
    }

}