#roomDetails {

    h4 {
        font-size: 20px;
        margin-bottom: 16px;
    }
    
    ul {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        margin-left: 82px;

        li {
            margin-bottom: 16px;
        }
    }

    .icons {
        display: flex;
        flex-wrap: wrap;
        margin-top: 20px;
        max-width: 480px;
    }

    .amenitie {
        display: flex;
        min-width: 120px;
    }

    .amenitie p {
        margin-right: 40px;
    }

    .ico {
        height: 34px;
        margin-right: 40px;
    }

    .booking {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .price {
        display: flex;
        flex-direction: row;
        align-items: flex-end;
        width: 100%;
    }

    .price p:first-child {
        margin-right: 130px;
    }

    .price h3 {
        font-size: 40px;
        margin: 10px 5px;
    }

}