#RoomPage {
    margin-bottom: 40px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 1200px;

    h1 {
        font-size: 32px;
        font-weight: 600;
        line-height: 72px;
    }

    .imageAndDetails {
        display: flex;
        flex-direction: row;
        margin-bottom: 20px;
    }


    .textDescriptionAndBookAndCalendar {
        display: flex;
        flex-direction: row;
        width: 100%;

        .textDescriptionAndBook {
            width: 100%;

            .roomTextDescription {
                text-align: start;
                width: 40vw;
                margin-bottom: 40px;
            }

            Form {
                display: flex;
                flex-direction: column;

                .chooseRoom {
                    display: flex;
                    flex-direction: row;

                    p {
                        margin: 0 20px 20px 0;
                    }

                    select {
                        height: 30px;
                        border: solid 1px #000;
                    }
                }

                .inputsWrap {
                    display: flex;
                    flex-direction: column;
                    margin-bottom: 20px;

                    .inputRow {
                        display: flex;
                        flex-direction: row;

                        .inputWrap {
                            display: flex;
                            align-items: flex-start;
                            width: 50%;

                            label {
                                width: 20%;
                                margin-right: 4px;
                            }

                            input {
                                border: solid 1px #000;
                                padding: 4px 6px;
                            }
                        }
                    }
                }

                button {
                    width: 20%;
                    height: 30px;
                    margin-left: 30%;
                    background-color: #DEC328;
                    border: solid 1px #000;
                }

                button:hover {
                    background-color: #c5ad24;
                    cursor: pointer;
                }
            }

            
        }

        .calendarWrap {

            #Calendar {
                header {
                    height: 50px;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    p {
                        margin: 10px;
                    }
                }
            }

            .calendarLegend {
                display: flex;
                flex-direction: row;
            }

            .legendElement {
                display: flex;
                flex-direction: row;
                margin: 5px 10px;
            }

            .legendElement>div {
                width: 20px;
                height: 20px;
                border: solid 1px;
            }
        }
    }

}